import axios from "axios";
import Grapesjs from "grapesjs";
import gjsBlocksBasic from "grapesjs-blocks-basic";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { API_URL } from "../../apiUtils/api-builder";
import BuilderConfig from "../../configs/builderConfig";
import "./builder.css";

const MainBuilder = () => {
	const [gEditor, setGeditor] = useState(null);

	const { name } = useParams();

	console.log(name);
	useEffect(() => {
		const editor = Grapesjs.init({
			// Indicate where to init the editor. You can also pass an HTMLElement
			container: "#editor",
			plugins: [gjsBlocksBasic],
			pluginsOpts: {
				gjsBlocksBasic: {},
			},
			storageManager: {
				autosave: false,
				stepsBeforeSave: 1,
				type: "remote",
				contentTypeJson: true,
				storeComponents: true,
				storeStyles: true,
				storeHtml: true,
				storeCss: true,
				urlStore: `${API_URL}/update-page/${name}`,
				urlLoad: `${API_URL}/single-page/${name}`,
				headers: {
					"Content-Type": "application/json",
				},
			},
		});
		setGeditor(editor);
		editor.Panels.addButton("options", [
			{
				id: "save-db",
				className: "fa fa-floppy-o",
				command: "save-db",
				attributes: { title: "Save DB" },
			},
		]);

		// Add the command
		editor.Commands.add("save-db", {
			run: function (editor, sender) {
				sender && sender.set("active", 0); // turn off the button
				editor.store();

				var htmldata = editor.getHtml();
				var cssdata = editor.getCss();
				console.log(editor);
				console.log(cssdata);

				editor.on("storage:store", function (e) {
					axios.post(`${API_URL}/update-page/${name}`, e);
				});
			},
		});

		editor.on("storage:load", function (e) {
			console.log("Loaded ", e);
		});
	}, [name]);

	const loadBuilder = () => {
		const editor = Grapesjs.init(BuilderConfig(name));
		setGeditor(editor);
	};

	gEditor && console.log(typeof gEditor.getHtml());

	return (
		<>
			<div id="editor"></div>

			{/* blocks */}
			{/* <div id="blocks"></div> */}
		</>
	);
};

export default MainBuilder;
